import { Component, Input } from '@angular/core';
// core version + navigation, pagination modules:
import Swiper from 'swiper';
import { Navigation, Pagination } from 'swiper/modules';
import axios from 'axios';
import { IWidgetSettings } from '../types';
import { WidgetType } from '../services/widgetType';


@Component({
  selector: 'lib-hello',
  templateUrl: './hello.component.html',
  styles: []
})
export class HelloComponent {
  @Input() name: string = '';
  @Input() widgetSettings: IWidgetSettings = {
    elementId: '',
    widgetId: '',
    version: '',
    inlineStyle: '',
    title: '',
    widgetType: WidgetType.RESP,
    config: null,
  };

  ngOnInit() {
    // init Swiper:
    const swiper = new Swiper('.swiper', {
      // configure Swiper to use modules
      modules: [Navigation, Pagination],
    });
    console.log(swiper);
    axios.get('https://jsonplaceholder.typicode.com/users')
      .then((response: any) => {
        // Успешный ответ: выведем список пользователей
        console.log('Список пользователей:', response.data);
      })
      .catch((error: any) => {
        // Обработка ошибок
        console.error('Произошла ошибка при запросе:', error);
      });
  }

  public postHelloMessage() {
    (parent || window).postMessage(JSON.stringify({
      event: "promotions",
      data: {},
      requestId: '00-575d6b495c1355ec5bf66ae432b601ef-2b0d7c6e513397e2-01',
      action: 'promise'
    }), '*');
  }
}