import { Injectable, Type } from '@angular/core';
import { HelloComponent } from '../hello/hello.component';
import { IPageWithTemplateComponentFactory, IPageWithTemplateWidget } from '../types';

@Injectable({ providedIn: 'root' })
export class PageWithTemplateComponentFactory implements IPageWithTemplateComponentFactory {
  private pageWithTemplateWidgetsMap: Record<
    string,
    Record<string, Type<any>>
  > = {
    'Vadzim.Echo': {
      '1.0': HelloComponent,
    },
  };

  public createComponentType(widgetId: string, version: string): Type<IPageWithTemplateWidget> | null {
    return this.pageWithTemplateWidgetsMap[widgetId]?.[version] || null;
  }
}
